import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'

import favicon from '../resources/favicon/favicon.ico'
import appleTouchIcon from '../resources/favicon/apple-touch-icon.png'
import favicon32x32 from '../resources/favicon/favicon-32x32.png'
import favicon16x16 from '../resources/favicon/favicon-16x16.png'

import Footer from '../components/Footer'
import './index.css'

const TemplateWrapper = ({ children }) => (
  <div className="flex flex-col font-sans min-h-screen text-grey-darkest">
    <Helmet
      title="Dylan Byars | Software Developer"
      meta={[
        { name: 'description', content: 'The portfolio page of Dylan Byars.' },
        { name: 'keywords', content: 'Dylan Byars, portfolio, front end, software developer' }
      ]}
      link={[
        { rel: 'shortcut icon', type: 'image/png', href: favicon },
        { rel: 'apple-touch-icon', sizes: '180x180', href: appleTouchIcon },
        { rel: 'icon', type: 'image/png', sizes: '32x32', href: favicon32x32 },
        { rel: 'icon', type: 'image/png', sizes: '16x16', href: favicon16x16 }
      ]}
    />
    {children()}
    <Footer />
  </div>
)

TemplateWrapper.propTypes = {
  children: PropTypes.func
}

export default TemplateWrapper



